import React, { Component } from "react";
import UserInfo from "./UserInfo";
import CustomButton from "./CustomButton";
import { Col, Row } from "react-bootstrap";

var componentToRender;
class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeUserId: "",
    };
  }

  handleClick = (User) => {
    componentToRender = <UserInfo user={User} />;
    this.setState({
      activeUserId: User.id === this.state.activeUserId ? "" : User.id,
    });
  };
  render() {
    return (
      <React.Fragment>
        <Row className="justify-content-center">
          <Col className="mt-3" lg={6}>
            <div
              className="card border-dark"
              style={{
                borderRadius: "25px",
                boxShadow: "0px 5px 8px grey",
              }}
            >
              <div
                style={{
                  borderTopLeftRadius: "25px",
                  borderTopRightRadius: "25px",
                }}
                className="card-header bg-dark text-white"
              >
                <h4>User List</h4>
              </div>
              <ul className="list-group list-group-flush">
                {this.props.Users.map((User, index) => (
                  <li
                    key={User.id}
                    className="list-group-item"
                    style={{
                      borderBottomLeftRadius: "25px",
                      borderBottomRightRadius: "25px",
                    }}
                  >
                    <div className="row justify-content-between">
                      <label className="col-xs-6 text-left pl-3">
                        <b>
                          {User.id}. {User.FirstName} {User.LastName}
                        </b>
                      </label>

                      <CustomButton
                        className="col-xs-6"
                        clickHandle={() => this.handleClick(User)}
                        buttonName={
                          this.state.activeUserId === User.id
                            ? "Click to See Less"
                            : "Click to See More"
                        }
                      />
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          </Col>
        </Row>
        <Row className="justify-content-center">
          {this.state.activeUserId ? componentToRender : null}
        </Row>
      </React.Fragment>
    );
  }
}

export default Users;
